.app {
  text-align: center;
  /* overflow: hidden; */
  height: 100vh;
  width: 100vw;
}

.navbar-wrapper {
  width: fill;
  background-color: #e1ebe7;
  position: fixed;
  font: 14pt 'Montserrat', sans-serif;
}
.navbar {
  width: fill;
  height: 350px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
}
.navbar-link {
  text-decoration: none;
  color: black;
}
.navbar-link:hover {
  color: #ad1a64;
}

.content-wrapper .bg-image {
  position: relative;
  /* width: 100%; */
  height: 400vh;

  /* top: 63px; */
  background-image: url('images/flower.jpg') ;
  /* top: -14vh; */
  background-position: bottom;
  background-size: cover ;
  background-repeat: no-repeat;
  opacity: 65%;
  z-index: -1;
}

.home {
  display: flex;
  justify-content: center;
}

.banner {
  position: fixed;
  /* top: 63px; */
  top: 80px;
  margin: 20px 0 20px 0;

  /* height: 55vh; */
  width: 80vw;
  min-height: 300px;
  min-width: 500px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fffbefca;
  /* border: thin black solid; */
  /* border-radius: 5px; */
  /* padding: 25px; */
}

.banner h1 {
  font: 60pt 'Cormorant Infant', serif;
  margin: 14px;
}

/* change this font */
.banner h2 {
  font: 16pt 'Montserrat', sans-serif;
  margin: 12px;
}
.banner p {
  font: 3vw 'Montserrat', sans-serif;
  margin: 0;
}


.rsvp-button {
  width: 130px;
  height: 35px;
  background-color: black;
}
.rsvp-button a {
  color: white;
  text-decoration: none;
}
